/*********************************************
:: SASS Setup
   Load your helper files
   Set them up inside variables
********************************************/
@import "sass-setup/helper-breakpoints";
@import "sass-setup/helper-typography";
@import "sass-setup/variables";

/*********************************************
:: Styleguide
********************************************/
.styleguide-main,
.styleguide-header,
.styleguide-footer{
    float: left;
    width: 100%;
    text-align: left;
    background: white;
}

 /* Styleguide Header/Footer */
.styleguide-header,
.styleguide-footer{
    float: left;
    width: 100%;
    padding: 20px 0px 20px 0px;
    border-top: 10px solid #363636;
    text-align: center;
    p{
        margin-bottom:0px;
    }
}

.styleguide-sidebar{
    display: block;
    max-width: 760px;
    margin: 0 auto;
}

.styleguide-footer{
    text-align: left;
    padding: 20px;
    margin-bottom: 0px;
    border-top: none;
    border-bottom: 10px solid #363636;
    p, a{
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 5px;
    }
}


/* Styleguide Block */
.styleguide-block{
    float:left;
    width: 100%;
    padding: 20px;
}
.styleguide-module-block{
    padding: 0px;
}

.styleguide-block .border-bottom-heading{
    margin-bottom: 10px;
    padding-bottom: 10px;
    margin-top: 30px;
    border-bottom: 1px dashed #999;
    &:first-of-type{
        margin-top: 0px;
    }
}


/* Styleguide Block Description */
.styleguide-block-description{
    float: left;
    width: 100%;
    padding: 20px;
    background: #ececec;
    border-bottom: 5px solid #A7A7A7;
    >*:last-child{
        margin-bottom: 0px;
    }
}


/* Styleguide Title */
.styleguide-block-title{
    display: inline-block;
    width: 100%;
    border-bottom: 5px solid #A7A7A7;
    font-size: 18px;
    line-height: 16px;
    padding: 20px;
    background: #484141;
    color: white;
}


/* Colours Styling */
.colour-block {
    float: left;
    width: 50%;
    height: 50px;
    margin-right: 2%;
    margin-bottom: 2%;
    border: 1px dashed $color-medium-grey;
    display: inline-block;
    text-align: center;

    @include mappy-bp(medium){
        width: 20%;
        height: 150px;
    }

    &:after{
        color: white;
        display: inline-block;
        padding-top: 10px;
    }
}

// Brand Colours
.color-brand-1-bg{
    background: $color-brand-1;
    &:after{content: "$color-brand-1 - #{$color-brand-1}";}
}
.color-brand-2-bg{
    background: $color-brand-2;
    &:after{content: "$color-brand-2 - #{$color-brand-2}";}
}
.color-brand-3-bg{
    background: $color-brand-3;
    &:after{content: "$color-brand-3 - #{$color-brand-3}";}
}

// Shades of Grey
.color-light-grey-bg{
    background: $color-light-grey;
    &:after{content: "$color-light-grey - #{$color-light-grey}"; color:$color-dark-grey;}
}
.color-grey-bg{
    background: $color-grey;
    &:after{content: "$color-grey - #{$color-grey}";}
}
.color-medium-grey-bg{
    background: $color-medium-grey;
    &:after{content: "$color-medium-grey - #{$color-medium-grey}"; color:$color-dark-grey;}
}
.color-dark-grey-bg{
    background: $color-dark-grey;
    &:after{content: "$color-dark-grey - #{$color-dark-grey}";}
}

// User Feedback
.color-message-bg{
    background: $color-message;
    &:after{content: "$color-message - #{$color-message}"; color:$color-dark-grey;}
}
.color-attention-bg{
    background: $color-attention;
    &:after{content: "$color-attention - #{$color-attention}";}
}
.color-success-bg{
    background: $color-success;
    &:after{content: "$color-success - #{$color-success}"; color:$color-dark-grey;}
}

// Social Media
.color-facebook-bg{
    background: $color-facebook;
    &:after{content: "$color-facebook - #{$color-facebook}";}
}
.color-twitter-bg{
    background: $color-twitter;
    &:after{content: "$color-twitter - #{$color-twitter}";}
}
.color-gplus-bg{
    background: $color-gplus;
    &:after{content: "$color-gplus - #{$color-gplus}";}
}
.color-youtube-bg{
    background: $color-youtube;
    &:after{content: "$color-youtube - #{$color-youtube}";}
}
.color-vimeo-bg{
    background: $color-vimeo;
    &:after{content: "$color-vimeo - #{$color-vimeo}";}
}


/* Font Family Collection */
.font-family-collection{
    font-size: 150%;
    .font1-light{
        font-family: $font-family-1;
        font-weight: $font-light;
    }
    .font1-regular{
        font-family: $font-family-1;
        font-weight: $font-normal;
    }
    .font1-semibold{
        font-family: $font-family-1;
        font-weight: $font-semibold;
    }
    .font1-bold{
        font-family: $font-family-1;
        font-weight: $font-bold;
    }
    .font1-extrabold{
        font-family: $font-family-1;
        font-weight: $font-extrabold;
    }
    .font2-light{
        font-family: $font-family-2;
        font-weight: $font-light;
    }
    .font2-regular{
        font-family: $font-family-2;
        font-weight: $font-normal;
    }
    .font2-semibold{
        font-family: $font-family-2;
        font-weight: $font-semibold;
    }
    .font2-bold{
        font-family: $font-family-2;
        font-weight: $font-bold;
    }
    .font2-extrabold{
        font-family: $font-family-2;
        font-weight: $font-extrabold;
    }
}

/* Icons Collection */
.icon-collection{
    font-size: 50px;
    line-height: 60px;

    // Hiding all the crap from the generated icomoon demo file
    h1, input.unitRight, .fs0.bshadow0{
        display: none;
    }

    .ptl{
        lost-utility: clearfix;
    }

    input[type=text]{
        background: white;
        border: none;
        color: black;
        opacity: 1;
        padding: 0px;
        font-size: 0.8em;
        line-height: 1.2;
        height:auto;
    }

    .glyph{
        margin-bottom: 10px;
        width: 100%;
        float: left;
        font-size: 50%;
        line-height: 1.6;

        @include mappy-bp(medium){
            lost-column: 1/2;
        }
        @include mappy-bp(large){
            lost-column: 1/3;
        }
        @include mappy-bp(largeish){
            lost-column: 1/4;
        }

        .pbs{
            background: $color-light-grey;
            padding: 5px;
        }

        .mls, fieldset{
            font-family: $font-family-1 !important;
            font-size: 0.8em;
            line-height: 1;
            display: inline-block;
            vertical-align: text-bottom;
        }

        fieldset{
            padding: 5px;
        }

    }

}