
//Placeholder image
$placeholder-image: "https://www.scandichotels.com/Static/img/placeholders/image-placeholder_3x2.svg";

//Breakpoint Sizes
$mediumish : 1024px;
$tablet : 600px;
$iphone5: 350px;

// Fonts: Base styles
$font-family-1: "Open Sans", "Arial", "Helvetica Neue", "Helvetica", sans-serif;
$font-family-2: "Open Sans", "Arial", "Helvetica Neue", "Helvetica", sans-serif;
$font-icon: "icomoon";

// Fonts: Weights
$font-light: 300;
$font-normal: 400;
$font-semibold: 600;
$font-bold: 700;
$font-extrabold: 900;

// Colors: Brand
$color-brand-1: #0a224c; //navy
$color-brand-2: #bed630; //lime
$color-brand-3: #000621; //dark-navy
$color-brand-4: #5ea5bb; //teal

// Colors: Shades of Grey
$color-grey: #979797;
$color-medium-grey: #ccc;
$color-dark-grey: #212121;
$color-light-grey: #ececec;

// Colors: Links
$color-link: $color-brand-1;
$color-link-hover: darken($color-link, 12%);

// Colors: User Feedback
$color-message: $color-medium-grey;
$color-attention: $color-brand-1;
$color-success: #38B449;

// Colors: Social Media
$color-facebook: #3b5998;
$color-twitter: #55acee;
$color-vimeo: #1ab7ea;
$color-youtube: #cd201f;
$color-gplus: #DC4E41;

//Page Height Calculator
$z-header: 999;
$header-height-desktop: 125px;
$header-height-mobile: 78px;
$header-width: 100%;

$footer-height-desktop: 155px;
$footer-height-mobile: 70px;









// Button Font Size
$button-small-font-size-map: (null: (0.6em, 1), mediumish: (0.8em, 1));
$button-default-font-size-map: (null: (0.8em, 1), mediumish: (1em, 1));
$button-large-font-size-map: (null: (1em, 1), mediumish: (1.2em, 1));

// Button Colour
$button-background-color: #FFFFFF;
$button-text-color: #090909;

// Button Padding
$button-small-padding: 10px 25px 10px 25px;
$button-default-padding: 10px 5px 10px 5px;
$button-large-padding: 15px 55px 15px 55px;

/*******************************************
// Form
/*

    Usage: 
    .input{
     background: $form-field-background-color;
     color: $form-field-text-color;
  }
  
*/

// Label Font Size
$label-font-size-map: (null: (0.938em, 1.7));

// Form Field Font Size
$form-field-font-size-map: (null: (0.938em, 1.7));

// Form Field Colours
$form-field-background-color: #fff;
$form-field-text-color: #000;
$form-field-border-color: #fff;
$form-field-hover-border-color: $color-brand-3;

// Form Field Dimensions
$form-field-height: 41px;
$form-field-height-large: 120px;
$form-field-padding: 6px 10px 6px 10px;

/*******************************************
// Breakpoints
/*

    Mappy breakpoints: https://github.com/zellwk/mappy-breakpoints
    Usage: 
    @include mappy-bp(small){
        stuff
    }
    
*/

$breakpoints: (tiny: 350px, small: 450px, smallish: 600px, medium: 768px, mediumish: 1024px, large: 1160px, largeish: 1280px, huge: 1440px, bigmac: 1920px);

//*******************************************
// Typography
/*

    Typi: https://github.com/zellwk/typi
    Usage: 
    @include typi($h1-map)
    .element{
    @include typi(whatever-map-youve-defined)
  }
  
*/

$typi: (null: (14px, 1.5));

$h1-map: (null: (4em, 1.2), mediumish: (4.19em, 1.3));

$h2-map: (null: (2.94em, 1.2), mediumish: (2.56em, 1.3));

$h3-map: (null: (1.313em, 1.2), mediumish: (2em, 1.3));

$h4-map: (null: (1.5em, 1.2), mediumish: (1.25em, 1.3));

$h5-map: (null: (0.875em, 1.2), mediumish: (1.15em, 1.3));

$h6-map: (null: (0.813em, 1.2), mediumish: (0.938em, 1.3));

$copy-map: (null: (0.938em, 1.7));

//*******************************************